import React, { useState, useEffect } from "react";

import { SearchBar, VideoList, VideoDetail } from "./components";

import youtube from "./api/youtube";
import './style.css'

const App = () => {

  const [videos, setVideos] = useState([]);

  useEffect(() => {
    async function Initializer() {
      const { data: { items: videos } } = await youtube.get("search", {
        params: {
          part: "snippet",
          maxResults: 32,
          //API1: AIzaSyAOxxFgJDynZZ4V7PMhZIKzQ6riReqytwI*
          //API2: AIzaSyDcWKaOPzPFvxDSY8351ZG55IIOUo0kMmI
          key: "AIzaSyAOxxFgJDynZZ4V7PMhZIKzQ6riReqytwI",
          q: "Trendings Musics",
        }
      });
      setVideos(videos);
    }
    Initializer();
  }, []);


  const [menu, setMenu] = useState(false);

  const [selectedVideo, setSelectedVideo] = useState({ id: {}, snippet: {} });

  function handelMenu() {
    if (!menu) {
      document.querySelector(".LeftPanel").style.left = "-3%";
      setMenu(true);
    }
    else {
      document.querySelector(".LeftPanel").style.left = "-100%";
      setMenu(false);
    }
  }
  return (
    <>
      <div className="Main_Container">

        <div className="Nav_bar">
          {!menu ?
            <button id="menu" onClick={handelMenu}><i className="fas fa-bars"></i></button> :
            <button id="menu" onClick={handelMenu}><i className="fas fa-times"></i></button>
          }
          <div>
            <img id="logo" alt="logo" src="https://see.fontimg.com/api/renderfont4/ZVZdq/eyJyIjoiZnMiLCJoIjo2MiwidyI6MTAwMCwiZnMiOjYyLCJmZ2MiOiIjRkZGRUZFIiwiYmdjIjoiI0ZGRkZGRiIsInQiOjF9/VS1UdWJl/roasting.png" height="30px" />
          </div>
          <SearchBar onSubmit={handleSubmit} />
          <div></div>
        </div>

        <div className="BodySection">
          <div className="LeftPanel">
            <p id="Heading"><i className="fas fa-newspaper"></i> News</p>
            <button id="left_btns" onClick={() => handleSubmit("News")}>News</button>
            <p id="Heading"><i className="fas fa-table-tennis"></i> Sports</p>
            <button id="left_btns" onClick={() => handleSubmit("Cricket")}>Cricket</button>
            <button id="left_btns" onClick={() => handleSubmit("Football")}>Football</button>
            <button id="left_btns" onClick={() => handleSubmit("Hockey")}>Hockey</button>
            <p id="Heading"><i className="fas fa-music"></i> Musics</p>
            <button id="left_btns" onClick={() => handleSubmit("Bollywood Songs")}>Hindi Songs</button>
            <button id="left_btns" onClick={() => handleSubmit("English Songs")}>English Songs</button>
            <button id="left_btns" onClick={() => handleSubmit("punjabi Songs")}>Punjabi Songs</button>
            <button id="left_btns" onClick={() => handleSubmit("Devotes Songs")}>Devotional Songs</button>
            <p id="Heading"><i className="fas fa-gamepad"></i> Gamings</p>
            <button id="left_btns" onClick={() => handleSubmit("Gamings")}>Live Streams</button>
            <p id="Heading"><i className="fas fa-baby"></i> Kids</p>
            <button id="left_btns" onClick={() => handleSubmit("Kid Rhyms")}>Rhyms</button>
            <button id="left_btns" onClick={() => handleSubmit("Cartoons")}>Cartoons</button>
            <button id="left_btns" onClick={() => handleSubmit("Funs")}>Funs</button>
          </div>

          <div className="RightPanel">



            {videos.length === 0 ?
              <div>
                {console.log(videos)}
                <h1><i className="fas fa-exclamation-triangle"></i> Server Down!! </h1> <br />
                <h4>We are Really Sorry, Please Check Back Later.</h4>
              </div>
              : <>
                <VideoDetail video={selectedVideo} />
                <VideoList videos={videos} onVideoSelect={setSelectedVideo} />
              </>}
          </div>
        </div>
      </div>
    </>
  );

  async function handleSubmit(searchTerm) {
    const { data: { items: videos } } = await youtube.get("search", {
      params: {
        part: "snippet",
        maxResults: 32,
        key: "AIzaSyAOxxFgJDynZZ4V7PMhZIKzQ6riReqytwI",
        q: searchTerm,
      }
    });
    setVideos(videos);
    setSelectedVideo(videos[0]);
  }
}

export default App;